@font-face {
    font-family: 'roboto-bold';
    src: url('./roboto/Roboto-Bold.ttf');
}
@font-face {
    font-family: 'roboto';
    src: url('./roboto/Roboto-Regular.ttf');
}
#logo{
    position:relative;
    width:200px;
    height: 50px;
    margin:0;
    padding:0;
}
body, html{
    background-image: url("./Images/sierra_original.jpg");
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0;
    font-family: roboto;
    overflow:hidden;
}
.section{
    color:white;
    background-color: black;
    height: 100vh;
    overflow:auto;
}
#leftside{
    font-size:0;
    float:left;
    
}
#rightside{
    width:100%;
    font-size:0;
    
}
i{
    padding-top:4px;
    width:auto;
}
.sectiontext{
    margin-top:5px;
    font-size:16px;
    margin-right:20px;
}
#sectionfield{
    height: 35px;
    background-color:#525459;
    font-family: roboto-bold;
    margin-left: 0;
    display:-ms-flexbox;
    display:flex;
    cursor: pointer;
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
}
.sectioncontent{
    display:-ms-flexbox;
    display:flex;
    cursor: pointer;
    height: 30px;
    color:white;
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
    -webkit-animation-duration: 0.3s!important;
            animation-duration: 0.3s!important;
}
.sectioncontent:hover, .folder:hover{
    background-color:grey;
}
.folder{
    display:-ms-flexbox;
    display:flex;
    cursor: pointer;
    font-family: roboto;
    height: 30px;
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
 
}
.navbar {
    display: -ms-flexbox;
    display: flex;
    background-color: #333;
    height: 50px;
    width: 100%;

}
.navbar a {
    color: white;
    padding: 14px 20px;
    text-decoration: none;
    text-align: center;
    font-size:15px;

}
.navbar a:hover {
    background-color: grey;
    color: black;
}
#brace-editor{
    width:initial!important;
    height:calc(100vh - 300px)!important;
}
::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
    background: #2196F3; 
}

::-webkit-scrollbar-thumb:hover {
    background: #1778c4; 
}
.space{
    padding-left: 17px;
    
}
.spaceclear{
    padding-left:0px!important;
}
.clear{
    color:white;
    cursor: pointer;
    margin-top:3px;
    font-size: 18px;
    -webkit-user-select:none;
       -moz-user-select:none;
        -ms-user-select:none;
            user-select:none;
}
.mdl-layout-title{
    font-size:30px!important;
}
#terminal{
    width: calc(100% - 170px);
    height: 250px;
    background: white; 
    
}


